document.getElementsByTagName('header')[0].hidden = true

function updateWho() {
  who = document.querySelector('input[name="who"]:checked').value
  if (who == "BOH") {
    document.getElementById("field-firstname").value = "Byunghyun"
    document.getElementById("field-lastname").value = "Oh"
    document.getElementById("field-birthday").value = "30/11/1983"
    document.getElementById("field-placeofbirth").value = "Seoul"
  } else if (who == "JLR") {
    document.getElementById("field-firstname").value = "Jean-Louis"
    document.getElementById("field-lastname").value = "Renaudot"
    document.getElementById("field-birthday").value = "24/04/1964"
    document.getElementById("field-placeofbirth").value = "Besan√ßon"
  }
}
updateWho()
document.getElementById("who").onclick = function() { updateWho() }

document.getElementById("field-address").value = "22 rue de la Paix"
document.getElementById("field-city").value = "Vincennes"
document.getElementById("field-zipcode").value = "94300"

fieldsToHide = ["field-firstname", "field-lastname", "field-birthday", "field-placeofbirth",
                "field-address", "field-city", "field-zipcode"];

for (n of fieldsToHide) {
  field = document.getElementById(n)
  field.hidden = true
  field.parentNode.parentNode.style.height = "0px"
}

document.getElementById("field-storedata").hidden = true
document.getElementById("cleardata").hidden = true

ps = document.getElementsByTagName('p');
for (var i = 0; i < ps.length; i++) {
  if (ps[i].innerText.startsWith("certifie que mon"))
    ps[i].innerText = ""
}

labels = document.getElementsByTagName('label');
for( var i = 0; i < labels.length; i++ ) {
  if (labels[i].htmlFor.startsWith("checkbox-")) {
    motif = labels[i].htmlFor.split('-')[1]
	labels[i].innerHTML = motif
	if (motif != "achats" && motif != "sport_animaux") {
		labels[i].hidden = true
	}
  } else if (labels[i].htmlFor.startsWith("field-")) {
	labels[i].hidden = true
  }
}

document.getElementById("checkbox-travail").hidden = true
